import Comment from "@/models/Comment";
import connectDB from "@/lib/mongodb";
import { NextResponse } from "next/server";

connectDB();

async function findCommentRecursive(comment, id) {
  if (comment._id.toString() === id) return comment;
  for (let r of comment.replies) {
    const found = await findCommentRecursive(r, id);
    if (found) return found;
  }
  return null;
}

export async function POST(req, { params }) {
  const { commentId } = params;
  const { text = "", emojis = [] } = await req.json();

  try {
    let comment = await Comment.findById(commentId); // top-level comment
    if (!comment) {
      // Search nested replies
      const allComments = await Comment.find(); // or filter by postId if needed
      for (let c of allComments) {
        const nested = await findCommentRecursive(c, commentId);
        if (nested) {
          comment = nested;
          break;
        }
      }
    }

    if (!comment) return NextResponse.json({ error: "Comment not found" }, { status: 404 });

    const newReply = {
      _id: new mongoose.Types.ObjectId(),
      text,
      author: req.user._id,
      emojis,
      likes: [],
      replies: [],
      createdAt: new Date(),
      updatedAt: new Date(),
    };

    comment.replies.push(newReply);
    await comment.save();

    return NextResponse.json({ success: true, comment: newReply });
  } catch (err) {
    console.error(err);
    return NextResponse.json({ error: "Server error" }, { status: 500 });
  }
}
